import React from 'react';
import {
    CartesianGrid,
    Legend,
    Line,
    LineChart,
    ReferenceLine,
    ResponsiveContainer,
    Tooltip,
    XAxis,
    YAxis
} from "recharts";

export const BasicGraph = ({data,dataKey,domain,height,children}) => {
    height = height ? height:400;
        return (
            <ResponsiveContainer width="90%" height={height}>
                <LineChart
                    data={data}>
                    <CartesianGrid strokeDasharray="3 3"/>
                    <XAxis dataKey="row"/>
                    <YAxis domain={domain}/>
                    <Tooltip/>
                    <Legend/>
                    <ReferenceLine x="13/3" stroke="red" label={{ value: 'Ny gr√¶nse for testning', angle: -45}}/>
                    <ReferenceLine x="15/4" stroke="blue" label={{ value: 'vuggestuer, b√∏rnehaver og 0-5 klasse √•bner', angle: -45}}/>
                    <ReferenceLine x="20/4" stroke="blue" label={{ value: 'Fris√∏rer, mass√∏rer, mfl √•bner', angle: -45}}/>
                    <ReferenceLine x="18/5" stroke="blue" label={{ value: 'Fase 2 gen√•bning: restauranter, storcentre, mfl', angle: -45}}/>
                    <ReferenceLine x="8/6" stroke="blue" label={{ value: 'Fase 3 gen√•bning:Kultur + 50 forsamlede, m.m.', angle: -45}}/>
                    <ReferenceLine x="18/8" stroke="blue" label={{ value: 'Fase 4 gen√•bning:Barer + 100 forsamlede, m.m', angle: -45}}/>

                    <ReferenceLine x="17/6" stroke="green" label={{ value: 'Ny opg√∏relsesmetode for indlagte', angle: -45}}/>
                    <Line type="monotone" dataKey={dataKey} stroke="#8884d8" activeDot={{r: 2}}/>
                    {children}
                </LineChart>
            </ResponsiveContainer>
        )
    }
;